body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.padding-bottom {
  padding-bottom: 2rem;
}
.flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.flex-container > div{
    margin: .5rem;
}

.section-header {
    margin: 1rem 1rem .5rem 1rem;
}

/* 16 columns of the grid */
.flex-basis-full {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
}

/* 3 columns of the grid */
.flex-basis-3 {
    -webkit-flex-basis: 15%;
            flex-basis: 15%;
}

/* 4 columns of the grid */
.flex-basis-4 {
    -webkit-flex-basis: 24%;
            flex-basis: 24%;
}

/* 5 columns of the grid */
.flex-basis-5 {
    -webkit-flex-basis: 30%;
            flex-basis: 30%
}

/* 6 columns of the grid */
.flex-basis-6 {
    -webkit-flex-basis: 36%;
            flex-basis: 36%
}

/* 7 columns of the grid */
.flex-basis-7 {
    -webkit-flex-basis: 45%;
            flex-basis: 45%;
}

.nested-flex-item {
    -webkit-flex-basis: calc((100% - 1rem) / 2);
            flex-basis: calc((100% - 1rem) / 2);
    box-sizing: border-box;
    margin-bottom: 1rem;
}

.nested-flex-item:nth-child(odd) {
    margin-right: 1rem;
}

.flex-center {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.card-content {
    margin: 1em;
}

.map-card-content {
    margin-top: 1rem;
}

.map {
    margin-top: 1rem;
    width: 100%;
    height: 300px;
}

.contacts {
    height: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: .5rem 0;
}

.side-margin {
    margin: 1rem 1rem 0 1rem;
}

.input-width {
    width: 200px;
    margin-right: 0.5rem;
}

.vertical-align {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.firms-list {
    width: 100%;
    height: 100%;;
    display: -webkit-flex;;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center; 
}

.firms-list-item {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-left: 4px solid transparent;
}

.main-info {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.image {
    height: 2.5rem;
    padding: 0 10px;
    border-radius: 50%;
}

.warning-border {
    border-left: 4px solid orange;
}

.negative-border {
    border-left: 4px solid red;
}

.counter {
    margin-right: 1rem;
    font-size: .875rem;
    font-weight: 400;
    color: #6a6d70;
}

.red-label {
    color: red;
}

.orange-label {
    color: orange;
}

.chart {
    width: 80%;
    margin-bottom: 1rem;
}

.table-list {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0 1rem;
}

.nested-flex-container {
    margin-right: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

@media(max-width: 1630px) {
    /* 12 columns of the grid */
    .flex-basis-full {
        -webkit-flex-basis: 100%;
                flex-basis: 100%;
    }

    /* 3 columns of the grid */
    .flex-basis-3 {
        -webkit-flex-basis: 20%;
                flex-basis: 20%;
    }

    /* 4 columns of the grid */
    .flex-basis-4 {
        -webkit-flex-basis: 23%;
                flex-basis: 23%;
    }

    /* 5 columns of the grid */
    .flex-basis-5 {
        -webkit-flex-basis: 28%;
                flex-basis: 28%
    }

    /* 6 columns of the grid */
    .flex-basis-6 {
        -webkit-flex-basis: 33%;
                flex-basis: 33%
    }

    /* 7 columns of the grid */
    .flex-basis-7 {
        -webkit-flex-basis: 51%;
                flex-basis: 51%;
    }

    .nested-flex-item {
        -webkit-flex-basis: calc((100% - 1rem) / 2);
                flex-basis: calc((100% - 1rem) / 2);
        box-sizing: border-box;
        margin-bottom: 1rem;
    }
}

@media(max-width: 1240px) {
    .hide-at-1240px {
        display: none;
    }

    .flex-basis-3, .flex-basis-4, .flex-basis-5 {
        -webkit-flex-basis: 48%;
                flex-basis: 48%;
    }
}

@media(max-width: 830px) {
    .hide-at-830px {
        display: none;
    }

    .flex-basis-3, .flex-basis-4, .flex-basis-5 {
        -webkit-flex-basis: 100%;
                flex-basis: 100%;
    }
}

.carousel-image {
    margin-bottom: -4px;
    width: 100%;
}

